import React, { useEffect, useState } from 'react';
import { addComponent } from '../service/studentService';
import { useNavigate } from 'react-router-dom';
import { Field, Formik, Form, ErrorMessage } from 'formik'
import * as Yup from 'yup';
import '../css/AddComponentForm.css'
import { getAllCountry } from '../service/conutryService';
import {toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
function AddComponentForm() {
    const [student, setStudent] = useState({ id: "", name: "", phone: "", email: "",gender:"Nam",hobby:["c Truyn"],country:"" });
    const [countryList,setCountryList] = useState([])
    const navigate = useNavigate()
    useEffect(()=>{
        const fectchData = async()=>{
            const list = await getAllCountry()
            setCountryList(list)
        }
        fectchData()
    })
    const handleSubmit = async (value) => {
        const student = {
            ...value,
            country:JSON.parse(value.country)
        }
        await addComponent(student);
        toast.success("Thm mi thnh cng",{
            autoClose: 1000
        })
        navigate('/student')
    }
    const handleValidate = Yup.object({
        // id: Yup.number().required("Yu cu khng  trng"),
        name: Yup.string().required("Yu cu khng  trng").matches(/^[a-zA-Z-\s]+[a-zA-Z-]+$/, "Tn cha ng nh dng"),
        phone: Yup.number(/^\d{9}$/, "Hy nhp 9 s").required("Yu cu khng  trng"),
        email: Yup.string().required("Yu cu khng  trng").matches(/^\S+@\S+\.\S+$/, "Hy nhp ng nh dng"),
        country: Yup.string().required("Yu cu chn")
    })
    return (
        <>
            <Formik initialValues={student} onSubmit={handleSubmit} validationSchema={handleValidate}>
                <Form>
                    {/* <div>
                        <label>ID :</label>
                        <Field type='text' name='id' />
                        <ErrorMessage style={{ color: 'red' }} name='id' component='div' />
                    </div> */}
                    <div>
                        <label>Name :</label>
                        <Field type='text' name='name' />
                        <ErrorMessage style={{ color: 'red' }} name='name' component='div' />
                    </div>
                    <div>
                        <label>Phone :</label>
                        <Field type='text' name='phone' />
                        <ErrorMessage style={{ color: 'red' }} name='phone' component='div' />
                    </div>
                    <div>
                        <label>Email :</label>
                        <Field type='text' name='email' />
                        <ErrorMessage style={{ color: 'red' }} name='email' component='div' />
                    </div>
                    <div>
                        <label>Gii Tnh :</label>
                        <Field type='radio' name='gender' value="Nam" /> Nam
                        <Field type='radio' name='gender' value="N" /> N
                        <ErrorMessage style={{ color: 'red' }} name='gender' component='div' />
                    </div>
                    <div>
                        <label>S Thch :</label>
                        <Field type='checkbox' name='hobby' value="c Truyn" /> c Truyn
                        <Field type='checkbox' name='hobby' value="Chi_Game" /> Chi game
                        <Field type='checkbox' name='hobby' value="c_sch" /> c sch
                        <ErrorMessage style={{ color: 'red' }} name='hobby ' component='div' />
                    </div>
                    <div>
                        <label>Qu Qun :</label>
                        <Field as='select' name='country'>
                        <option value={''}>Select</option>
                            {countryList.map((m)=>(
                            <option value={JSON.stringify(m)}>{m.name}</option>
                            ))}
                        </Field>
                        <ErrorMessage style={{ color: 'red' }} name='country' component='div' />
                    </div>
                    <button type='submit'>Save</button>
                </Form>
            </Formik>
        </>
    )
}
export default AddComponentForm;